@charset "UTF-8";
.App {
  display: flex;
  min-height: 100vh;
  flex-direction: column; }
  .App .content {
    flex: 1; }
  .App .header {
    background-color: #fafafa;
    height: 60px; }
    .App .header .selected {
      color: #333 !important;
      padding-bottom: 5px;
      border-bottom: 1.5px solid #333; }
    .App .header .box {
      width: 70%;
      height: 100%;
      margin: 0px auto;
      padding: 0px 12px;
      display: flex;
      align-items: center;
      justify-content: space-between; }
      .App .header .box a:hover {
        text-decoration: none; }
      .App .header .box .boxleft {
        font-size: 20px;
        color: #333333; }
      .App .header .box .boxleft::after {
        content: "◦◦◦";
        margin-left: 0.5em; }
      .App .header .box .boxright {
        display: inline-flex;
        justify-content: flex-start; }
        .App .header .box .boxright p {
          margin: 0px 10px;
          font-size: 14px;
          color: #666666; }
      .App .header .box div {
        cursor: pointer; }
  .App .footer {
    text-align: center;
    padding: 50px 0px;
    color: #555;
    background: #fafafa;
    border-top: 1px solid #f2f2f2; }
    .App .footer p {
      padding: 2px 0px; }
